import { time } from '@amcharts/amcharts5';
import { useState } from 'react';
import './App.css';
import logo from './logo.svg';
import {Motion} from "./MotionClass.js"
const bootstrap = require('bootstrap')


function App() {
  
  function speedHandler(e){
   setSpeed(e.target.value) 
  }

  function angleHandler(e){
    setAngle(e.target.value) 
   }

   function massHandler(e){
    setMass(e.target.value) 
   }

   function surfaceHandler(e){
    setSurface(e.target.value) 
   }

   function ratioHandler(e){
    setRatio(e.target.value) 
   }

  function submitValues(e) {
    e.preventDefault();

    const task=new Motion(parseFloat(speed),parseFloat(angle), parseFloat(mass), parseFloat(surface), parseFloat(ratio))
    task.decomposingVector()
    task.findDistance()
    setDistance(task.distance)
    setTime(task.time)
}

  const [speed,setSpeed]=useState('')
  const [angle,setAngle]=useState('')
  const [mass,setMass]=useState('')
  const [surface,setSurface]=useState('')
  const [ratio,setRatio]=useState('')
  const [distance,setDistance]=useState('')
  const [time,setTime]=useState('')
  return (
    <div className="App">
      <header></header>
      <main>
        <div>
          <form>
          <input onChange={speedHandler} placeholder='' value={speed}></input>
          <input onChange={angleHandler} placeholder=''></input>
          <input onChange={massHandler} placeholder=''></input>
          <input onChange={surfaceHandler} placeholder=''></input>
          <input onChange={ratioHandler} placeholder=' '></input>
          <button onClick={submitValues}></button>
          </form>
         

        </div>
        <div>
          <p>: {speed}</p>
          <p>: {angle}</p>
          <p>: {distance}</p>
          <p>: {time}</p>
        </div>
      </main>
      <footer></footer>
    </div>
  );
}

export default App;
